@import "utils";
@import "fonts";
@import "typography";

body {
  background: #fff;
  margin: 0;
  padding-bottom: 540px
}

.container {
  margin: 0 auto;
  max-width: 940px;
  &:after, &:before {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
    *zoom: 1;
  }
}

.hero {
  background-size: cover;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-image: url(../images/header-back.png);
  .container {
    position: relative;
    height: 100vh;
    min-height: 500px;
  }
}

.header {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  text-align: center;
  &__logo {
    position: absolute;
    top: -46px;
    left: 50%;
    transform: translate(-50%,0);
    background-image: url(../images/header-logo.png);
    background-repeat: no-repeat;
    width: 115px;
    height: 76px;
  }
  &__phone {
    float: left;
    text-decoration: none;
    color: #fff;

  }
  &__call {
    float: right;
    text-decoration: none;
    color: #fff;
    border-bottom: 1px dashed #fff;
    text-transform: uppercase;
  }
}

.nav {
  position: absolute;
  bottom: 60px;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  a {
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
  }
}


.wrapper {
  background: #fff;
  position: relative;
  z-index: 200;
  box-shadow: 0 0 10px rgba(#000,.5);

}
.footer {
  height: 540px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-image: url(../images/footer-back.png);
  position: fixed;
  bottom: 0;
  z-index: 100;
  left: 0;
  right: 0;
  &__copy {
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    font-size: 16px;
    bottom: 66px;
    left: 0;
    right: 0;
    position: absolute;
  }
  &__logo {
    background-image: url(../images/footer-logo.png);
    background-repeat: no-repeat;
    width: 115px;
    height: 76px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.section {
  padding: 130px 0px;
  position: relative;
  &__bottom {
    margin-top: 30px;
    text-align: center;
  }
  &:nth-child(odd) {
    background: #eee;
  }
  &__left {
    float: left;
    width: 50%;
  }
  &__right {
    float: right;
    width: 33%;
  }
  &__title {
    position: absolute;
    left: 50%;
    top: -55px;
    transform: translate(-50%,0);
    font-size: 48px;
    font-weight: 100;
    color: #000;
    padding-bottom: 5px;
    text-transform: uppercase;
    .icon {
      position: absolute;
      bottom: -12px;
      left: 50%;
      transform: translate(-50%,0);
    }
    &:after,&:before {
      content: "";
      width: calc(50% - 20px);
      position: absolute;
      height: 2px;
      background: #1cabec;
      bottom: 0;
    }
    &:before {
      left: 0;
    }
    &:after {
      right: 0;
    }
  }
}

.best {
  &__motto {
    float: left;
    width: 33%;
    font-size: 48px;
    text-transform: uppercase;
    font-weight: 100;
    strong {
      font-weight: 600;
    }
    span {
      font-weight: 100;
      color: #1babec;
    }
  }
  &__text {
    float: right;
    width: 66%;
  }
}

.apart {
  margin-bottom: 30px;
  .apart__photo {
    background-image: url(../images/footer-back.png);
  }
  h2 {
    font-weight: 600;
    font-size: 16px;
  }
  &__photos {
    display: flex;
    margin-bottom: 12px;
  }
  &__photo {
    height: 260px;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    flex: 1;
    &:first-child {
      margin-right: 20px;
    }
  }
  &_mod-right &__photo {
    &:first-child {
      flex: 2;
    }
  }
  &_mod-left &__photo {
    &:last-child {
      flex: 2
    }
  }
}

.testis {
  display: flex;
  justify-content: space-between;
}
.t {
  text-align: center;

  &__photo {
    height: 150px;
    width: 150px;
    background-size: cover;
    background-position: 50% 50%;
    margin: 0 auto;
    position: relative;
    display: inline-block;
    &:before {
      content: ' ';
      position: absolute;
      left: -25px;
      top: 50%;
      margin-top: -15px;
      width: 35px;
      height: 50px;
      background: url(../images/quotes.png);
      background-repeat: no-repeat;
    }
  }
  &__name {
    margin-top: 10px;
    font-weight: bold;
    font-size: 16px;
    color: #000;
  }
  &__position {
    font-style: italic;
    font-size: 16px;
    color: #696973;
    margin-top: 5px;
  }
  &__descr {
    line-height: 1.37;
    font-size: 16px;
    text-align: center;
    color: #000;
    margin-top: 20px;
  }
}

.jobs {
  background-image: url(../images/jobs.png);
}
.alisher {
  background-image: url(../images/alisher.png);
}
.alexey {
  background-image: url(../images/alexey.png);
}


ul {
  list-style-type: none;
}

.textual {
  font-size: 16px;
  line-height: 1.5;
  color: #000;
  h2 {
    font-size: 32px;
    line-height: 1.1;
    font-weight: normal;
    margin-bottom: 1em;
  }
  p {
    margin-bottom: 1em;
  }
  ol {
    list-style: decimal;
    padding-left: 0;
  }
  ol, ul {
    margin-bottom: 1px;
  }
  &_big {
    font-size: 24px;
    line-height: 1.3;
    a {
      text-decoration: none;
      color: #000;
    }
  }
}

.form {
  &__field {
    margin-bottom: 10px;
  }
}

.input, .textarea {
  text-align: center;
  line-height: normal;
  height: 52px;
  background: #eee;
  font-weight: 600;
  color: #393939;
  text-transform: uppercase;
  width: 100%;
  border: none;
  &:placeholder {
    font-size: 14px;
  }
}

.textarea {
  height: 100px;
  padding: 20px 0;

}

.button {
  font-size: 14px;
  line-height: 52px;
  display: block;
  background: #1cabec;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  width: 100%;
  &_inline {
    display: inline-block;
    padding: 0 60px;
    width: auto;
  }
}
